@import url('https://fonts.googleapis.com/css?family=Montserrat:100,200,300,600,700');
body {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  background-color: #fff;
  margin: 0 auto;
  padding: 0;
  min-height: 140vh;
  margin-bottom: 10vh;
}
button {
  outline: none;
  cursor: pointer;
}
.header {
  overflow: hidden;
  text-align: left;
  width: 90%;
  margin: 0 auto;
  height: 100px;
}
.header h1 {
  display: inline-block;
  color: #000;
  font-size: 36px;
  font-weight: 100;
  margin-top: 10px;
}
.header h1 span {
  font-weight: 700;
  display: block;
  font-size: 20px;
}
.header .refresh {
  cursor: pointer;
  display: inline;
  font-weight: 300;
  margin-top: 7vh;
  float: right;
  padding: 0 20px;
}
.container {
  margin-top: 6vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.select-rates {
  border-radius: 3px;
  padding: 16px 0;
  height: 200px;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: 0 4vw;
}
.select-rates::-webkit-scrollbar {
  width: 0.5em;
}
.select-rates::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
  border-radius: 3px;
}
.select-rates li {
  color: #111;
  margin: 0 1em 0 1.5em;
  cursor: pointer;
}
.select-rates li img {
  border-radius: 3px;
  width: 7vw;
  margin: 2px 0;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  -webkit-transition: -webkit-transform 100ms ease;
  transition: -webkit-transform 100ms ease;
  -o-transition: transform 100ms ease;
  transition: transform 100ms ease;
  transition: transform 100ms ease, -webkit-transform 100ms ease;
}
.select-rates li img:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}
svg.right {
  poaition: relative;
  -webkit-transform: rotate(90deg) scale(0.6) translateX(10px);
      -ms-transform: rotate(90deg) scale(0.6) translateX(10px);
          transform: rotate(90deg) scale(0.6) translateX(10px);
}
.from-currency,
.to-currency {
  padding: 20px;
  color: #111;
}
.from-currency h3,
.to-currency h3 {
  margin: 1vh;
  font-weight: 600;
}
.arrow-container {
  margin: 0;
  padding: 0;
}
input[type=number] {
  text-align: center;
  font-size: 24px;
  outline: 0;
  height: 50px;
  padding: 12px 20px 12px 30px;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 3px solid #ddd;
  -webkit-transition: border 500s ease-in;
  -o-transition: border 500s ease-in;
  transition: border 500s ease-in;
  display: block;
}
input[type=number]:focus {
  border: #fff;
  border-bottom: 3px solid #ddd;
}
input[type=button][value=Save] {
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 3px;
  display: block;
  float: right;
  margin: 20px 0;
  padding: 6px 12px;
  color: #fff;
  background-color: #5bc0de;
  cursor: pointer;
  -webkit-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
  outline: none;
}
input[type=button][value=Save]:hover {
  -webkit-transform: scale(1.06);
      -ms-transform: scale(1.06);
          transform: scale(1.06);
  background-color: #3AB0D3;
}
.converted-amount {
  margin: 0 auto;
  width: 20vw;
  font-size: 32px;
  background-color: #61d262;
  font-weight: 400;
  border-radius: 4px;
  color: #fff;
  -webkit-box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}
.converted-amount p {
  padding: 0 10px;
  margin: 0;
}
.saved-exchanges li {
  position: relative;
  margin: 0 auto;
  width: 90%;
  height: 30px;
  max-width: 400px;
  border-radius: 6px;
  padding: 0 0 4px 20px;
  text-align: left;
}
.saved-exchanges li:nth-child(odd) {
  background-color: #ccc;
}
.saved-exchanges li:nth-child(even) {
  background-color: #f5f5f5;
}
.saved-exchanges li p {
  display: inline-block;
  margin: 0;
  text-align: left;
}
.saved-exchanges li button.delete-button {
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  background-color: firebrick;
  color: #fff;
  margin: 4px;
  padding: 2px 4px;
  border: none;
  border-radius: 6px;
}
@media only screen and (max-width: 900px) {
  .converted-amount {
    width: 40vw;
  }
  .from-rates,
  .to-rates {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
  }
  .from-rates li,
  .to-rates li {
    margin: 0;
  }
  .from-rates li img,
  .to-rates li img {
    width: 96%;
  }
  .saved-exchanges li button.delete-button {
    padding: 6px;
    margin: 4px;
    margin-bottom: 2px;
  }
  input[type=number] {
    width: 40vw;
  }
}
@-webkit-keyframes "spinner" {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes "spinner" {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
.spinner:before {
  position: absolute;
  content: '';
  top: 0;
  left: 50%;
  width: 100%;
  height: 100%;
  border-radius: 500rem;
  border: 0.2em solid rgba(0, 0, 0, 0.1);
  width: 2.28571429rem;
  height: 2.28571429rem;
  margin: 0;
}
.spinner:after {
  position: absolute;
  content: '';
  top: 0;
  left: 50%;
  width: 100%;
  height: 100%;
  -webkit-animation: spinner 0.6s linear;
          animation: spinner 0.6s linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  border-radius: 500rem;
  border-color: #767676 transparent transparent;
  border-style: solid;
  border-width: 0.2em;
  -webkit-box-shadow: 0 0 0 1px transparent;
          box-shadow: 0 0 0 1px transparent;
  width: 2.28571429rem;
  height: 2.28571429rem;
  margin: 0;
}



